.about__container,
.info__list,
.stats {
  grid-template-columns: repeat(2, 1fr);
}

.info__list {
  row-gap: 20px;
  margin-bottom: 32px;
}

.section__subtitle {
  font-size: var(--h3-font-size);
  font-weight: var(--weight-600);
  margin-bottom: 20px;
}

.info__list .info__title,
.info__list info__description {
  font-size: var(--small-font-size);
}

.info__list .info__description {
  color: var(--title-color);
  font-weight: var(--weight-600);
}

.about__img {
  background: url(../../assets/img/photo31.jpg);
  border-radius: 50%;
  background-size: cover;
  justify-self: center;
  background-repeat: no-repeat;
  background-position: center; /*шобы выровнять изображение*/
  box-shadow: inset 0 0 0 8px var(--first-color);
  width: 250px;
  height: 250px;
  animation: profile__animate-about 8s ease-in-out infinite 1s;
}

@keyframes profile__animate-about {
  0% {
    border-radius: 70% 50% 40% 80%/70% 40% 80% 50%;
  }
  50% {
    border-radius: 40% 70% 80% 50%/60% 70% 40% 70%;
  }
  100% {
    border-radius: 70% 50% 40% 80%/70% 40% 80% 50%;
  }
}

.stats {
  margin-top: 3rem;
}

.stats__box {
  border: 1px solid var(--border-color);
  padding: 20px 30px 25px;
  border-radius: 5px;
}

.stats__no {
  color: var(--first-color);
  font-size: var(--h1-font-size);
  line-height: 1.2;
}

.stats__title {
  font-size: var(--small-font-size);
  line-height: 1.6;
  padding-left: 45px;
  position: relative;
}

.stats__title::before {
  content: "";
  position: absolute;
  left: 0;
  top: 25%;
  width: 30px;
  height: 1px;
  background-color: var(--text-color);
}

.separator {
  border-top: 1px solid var(--border-color);
  max-width: 40%;
  margin: 72px auto 56px;
}

.subtitle__center {
  text-align: center;
  margin-bottom: 50px;
}

.skills__container {
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  row-gap: 48px;
}

.progress__circle {
  width: 120px;
  height: 120px;
}

.CircularProgressbar-path {
  stroke: var(--first-color) !important;
  stroke-linecap: butt !important;
}

.CircularProgressbar-trail {
  stroke: var(--container-color) !important;
}

.CircularProgressbar-text {
  fill: var(--title-color) !important;
  font-family: var(--second-font) !important;
  font-size: var(--large-font-size) !important;
}

.skills__title {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  font-weight: var(--weight-400);
  text-align: center;
  margin-top: 24px;
}
